import { firebase } from 'src/guards/firebase/Firebase';
import Papa from 'papaparse';

// Function to sanitize keys
function sanitizeKey(key) {
  return key ? String(key).replace(/[\.\#\$\/\[\]\s]/g, '_') : '_';
}

// Function to create a default object
function createDefaultObject(headers) {
  return headers.reduce((defaultData, key) => {
    defaultData[sanitizeKey(key)] = '';

    return defaultData;
  }, {});
}

// Function to merge data into the default object
function mergeDataToDefaultObject(defaultObject, data) {
  const dataObject = { ...defaultObject };
  for (const key in data) {
    if (data.hasOwnProperty(key)) {
      const sanitizedKey = sanitizeKey(key);
      if (dataObject.hasOwnProperty(sanitizedKey)) {
        if (data[key] != null && data[key] != undefined) {
          dataObject[sanitizedKey] = data[key];
        }
      } else {
        console.warn(
          `data has a unique key not available in the default object.\nKey: ${key}, Value: ${data[key]}`,
        );
      }
    }
  }

  return dataObject;
}

