// Function to handle CSV file parsing, processing, and logging in batches
async function handleCSVFile(database, file, uploadLimit, batchSize) {
  try {
    let defaultObject = {};
    const results = await new Promise((resolve) => {
      Papa.parse(file, {
        header: true,
        dynamicTyping: true,
        skipEmptyLines: true,
        complete: function (results) {
          // Create the default object
          defaultObject = createDefaultObject(results.meta.fields);
          resolve(results);
        },
      });
    });

    const jsonArray = results.data;
    const failedRows = [];

    // Split data into batches
    const batches = [];
    if (uploadLimit < 0) uploadLimit = jsonArray.length / batchSize + 1;
    for (let i = 0; i < jsonArray.length && batches.length < uploadLimit; i += batchSize) {
      const batch = jsonArray.slice(i, i + batchSize);
      batches.push(batch);
    }

    // Process each batch
    for (const batch of batches) {
      const batchPromises = batch.map((data) => processCSVRow(data, defaultObject, database));

      const batchResults = await Promise.all(batchPromises);

      for (let i = 0; i < batchResults.length; i++) {
        if (!batchResults[i]) {
          failedRows.push(mergeDataToDefaultObject(defaultObject, batch[i]));
        }
      }
    }

    if (failedRows.length > 0) {
      console.log('Failed rows:', failedRows);

      // Convert failedRows to CSV format
      const csv = Papa.unparse(failedRows);

      // Define the file path where you want to save the CSV file
      const outputPath = 'failed_upload_data.csv';

      // Write the CSV data to a file
      fs.writeFileSync(outputPath, csv);
    }
  } catch (error) {
    console.error('Error reading CSV data:', error);
  }
}

// Main function to handle CSV file selection
async function uploadCsvData(e, uploadLimit = -1, batchSize = 10) {
  const fileInput = document.createElement('input');
  fileInput.type = 'file';
  fileInput.accept = '.csv';

  fileInput.addEventListener('change', (event) => {
    const selectedFile = event.target.files[0];
    if (!selectedFile) {
      console.warn('No file selected.');

      return;
    }

    handleCSVFile(firebase.database(), selectedFile, uploadLimit, batchSize).catch((error) => {
      console.error('Error handling CSV file:', error);
    });
  });

  fileInput.click();
}

export { uploadCsvData };
